import cancelCabinet from './cancelCabinet';

const { default: Swal } = require('sweetalert2');
const { database } = require('../../configs/firebase.config');
const { default: writeUserData } = require('./writeUserData');

const enrollCabinet = (
  cabinetTitle,
  userCabinetTitle,
  userCabinetIdx,
  select,
  currentUserID,
  userId,
  currentUserName,
) => {
  if (userCabinetTitle === 0 && userCabinetIdx === 0) {
    database.ref(`cabinet/${cabinetTitle}/item/${select}`).transaction(
      (cabinet) => {
        if (cabinet === 0) {
          return currentUserID;
        }
        // eslint-disable-next-line no-useless-return
        return;
      },
      (error, committed, snapshot) => {
        if (error) {
          Swal.fire({
            icon: 'error',
            title: '  ',
            text: `  .`,
            showConfirmButton: true,
            width: '25rem',
            timer: 5000,
          });
        } else if (!committed) {
          Swal.fire({
            icon: 'error',
            title: '  ',
            text: `     .`,
            showConfirmButton: true,
            width: '25rem',
            timer: 5000,
          });
        } else {
          writeUserData(
            userId,
            currentUserName,
            currentUserID,
            cabinetTitle,
            select,
          );
          Swal.fire({
            icon: 'success',
            title: '  ',
            text: `${select}  `,
            showConfirmButton: true,
            width: '25rem',
            timer: 5000,
          });
        }
      },
    );
  } else {
    Swal.fire({
      icon: 'question',
      title: '  ',
      html:
        '   .<br/>' +
        '  ?',
      showConfirmButton: true,
      showCancelButton: true,
      confirmButtonText: '',
      cancelButtonText: '',
      width: '35rem',
    }).then((result) => {
      if (result.isConfirmed) {
        cancelCabinet(
          userCabinetTitle,
          userCabinetIdx,
          currentUserID,
          userId,
          currentUserName,
        );
      }
    });
  }
};

export default enrollCabinet;
