import React from 'react';
import styled from 'styled-components';
import { CSSTransitionGroup } from 'react-transition-group';
import { TextField, Button, makeStyles, Img } from '@material-ui/core';
import PropTypes from 'prop-types';
import Logo from '../image/softwareLogo_origin.png';
import { Mobile, Default } from '../MediaQuery';
import '../Pages/Fadeout.css';
import backwards from '../image/Backward.png';

const Container = styled.div`
  display: flex;
  justify-contents: center;
  align-items: center;
  flex-direction: column;
  width: 100%;
  height: 98vh;
`;

const MobileDivider = styled.div`
  border-bottom: 2px solid lightgray;
  width: 85%;
  margin: 5vh 1vw;
`;

const useStyles = makeStyles((theme) => ({
  backwards: {
    opacity: 0.6,

    '&:hover': {
      backgroundColor: 'transparent',
      opacity: 1,
    },
  },
}));

const SignUpMobile = (props) => {
  const {
    SignUpSubmit,
    linktoLogin,
    onIdHandler,
    onPasswordHandler,
    onStudentIdHandler,
    onNameHandler,
  } = props;
  const classes = useStyles();
  return (
    <CSSTransitionGroup
      transitionName="homeTransition"
      transitionAppear
      transitionAppearTimeout={500}
      transitionEnter={false}
      transitionLeave={false}
    >
      <Container>
        <img src={Logo} alt="logo" width="80vw" style={{ margin: '2vh 0 0' }} />
        <p
          style={{
            fontSize: '1rem',
            fontWeight: 'bold',
            letterSpacing: '0.5vw',
            borderBottom: '0.1vw solid black',
            margin: '0.7rem 0 0',
          }}
        >
          SEJONG UNIV
        </p>
        <p
          style={{
            fontSize: '0.8rem',
            fontWeight: 'bolder',
            letterSpacing: '0.4vw',
            margin: '0.15rem 0 0',
          }}
        >
           
        </p>

        <form
          onSubmit={SignUpSubmit}
          name="signUp"
          noValidate
          autoComplete="off"
          style={{
            justifyContent: 'center',
            flexDirection: 'column',
            display: 'flex',
            margin: '0.5vh 0',
          }}
        >
          <div
            style={{
              display: 'inline-flex',
              flexDirection: 'row',
              width: '100%',
              marginTop: '1rem',
              borderTop: '2px solid lightgray',
            }}
          >
            <Button
              style={{
                position: 'absolute',
                height: 'auto',
              }}
              onClick={linktoLogin}
              className={classes.backwards}
            >
              <img
                src={backwards}
                alt="toLogin"
                width="9vw"
                style={{ margin: '5vh 8vw auto 0' }}
              />
            </Button>

            <div style={{ width: '100%' }}>
              <p
                style={{
                  fontSize: '4vh',
                  fontWeight: 'bold',
                  width: '100%',
                  textAlign: 'center',
                }}
              >
                
              </p>
            </div>
          </div>
          <TextField
            id="id"
            label="Email"
            placeholder="  "
            type="email"
            variant="outlined"
            onChange={onIdHandler}
            style={{ width: '80vw', margin: '1vh 0.1vw' }}
          />
          <TextField
            id="password"
            label="Password"
            placeholder="6   ."
            type="password"
            variant="outlined"
            onChange={onPasswordHandler}
            style={{ width: '80vw', margin: '1vh 0.1vw' }}
          />
          <TextField
            id="studentID"
            label=""
            placeholder=" ."
            type="text"
            variant="outlined"
            onChange={onStudentIdHandler}
            style={{ width: '80vw', margin: '1vh 0.1vw' }}
          />
          <TextField
            id="name"
            label=""
            placeholder=" ."
            type="text"
            variant="outlined"
            onChange={onNameHandler}
            style={{ width: '80vw', margin: '1vh 0.1vw' }}
          />
          <Button
            variant="contained"
            type="submit"
            style={{
              width: '80vw',
              height: '2.5rem',
              backgroundColor: 'rgb(63,81,181)',
              color: 'white',
              border: '1px solid rgb(63,81,181)',
              marginTop: '1vh',
            }}
          >
            
          </Button>
        </form>
        <MobileDivider />
      </Container>
    </CSSTransitionGroup>
  );
};

SignUpMobile.propTypes = {
  SignUpSubmit: PropTypes.func.isRequired,
  linktoLogin: PropTypes.func.isRequired,
  onIdHandler: PropTypes.func.isRequired,
  onPasswordHandler: PropTypes.func.isRequired,
  onStudentIdHandler: PropTypes.func.isRequired,
  onNameHandler: PropTypes.func.isRequired,
};

export default SignUpMobile;
